import React from "react";
import ReactHtmlParser from "react-html-parser";
import { Routes, Route, useLocation } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.css";
import { Container, Row, Col } from "react-bootstrap";
import { CToastify, CPage, CHeader } from "./Component";
import { AnimatePresence } from "framer-motion/dist/framer-motion";
import "./styles.css";

export default function App(props) {
  const state = {
    lang: props.value.lang,
    assetsPath: props.value.assetsPath,
    origin: props.value.origin,
    dimention: props.value.dimention,
    breakpoint: props.value.breakpoint,
    pRatio: props.value.pRatio,
    site: props.value.site
  };

  const location = useLocation();

  return (
    <Container className="m-0 p-0 App" fluid>
      <CHeader
        value={{
          menu: state.site.menu,
          logo: state.site.logo,
          assetsPath: state.assetsPath,
          origin: state.origin,
          lang: state.lang
        }}
      />
      <AnimatePresence>
        <Routes location={location} key={location.pathname}>
          {state.site.menu.map((item, index) => (
            <Route
              key={Math.random()}
              path={state.lang + item.url}
              element={
                <CPage
                  value={{
                    lang: state.lang,
                    assetsPath: state.assetsPath,
                    origin: state.origin,
                    dimention: state.dimention,
                    breakpoint: state.breakpoint,
                    pRatio: state.pRatio,
                    pages: state.site.pages,
                    pageId: item.name
                  }}
                />
              }
            />
          ))}
        </Routes>
      </AnimatePresence>
      <br></br>
      <footer className="mt-auto bg-dark text-white p-3 py-5">
        <Row className="mx-auto col-12 col-xxl-7 col-xl-8 col-lg-9 col-md-10 col-sm-12">
          {state.site.footer.map((item, id) => (
            <Col key={Math.random()}>{ReactHtmlParser(item.text)}</Col>
          ))}
        </Row>
        <Row className="text-center d-block pt-3">{state.site.copyright}</Row>
      </footer>
      <CToastify />
    </Container>
  );
}
