//import { StrictMode } from "react";
import { createRoot } from "react-dom/client";
import { BrowserRouter } from "react-router-dom";
import axios from "axios";

import App from "./App";

const rootElement = document.getElementById("root");
const root = createRoot(rootElement);

//Detect Screen Size
const { innerWidth, innerHeight, devicePixelRatio } = window;
let breakpoint = "";
const w = innerWidth * devicePixelRatio;
if (w <= 576) {
  breakpoint = "_sm";
} else if (w <= 768) {
  breakpoint = "_md";
} else if (w <= 1200) {
  breakpoint = "_xl";
} else if (w <= 1400) {
  breakpoint = "_xxl";
} else {
  breakpoint = "";
}

//Detect Lang
const fakeLocation = {
  origin: "https://b1r.site"
};
let { origin, pathname } = document.location;
origin = fakeLocation.origin;

const pathArr = pathname.split("/");
const lang = pathArr[1];

const assetsPath = "/assets0867";
window.pageId = "index";
document.getElementsByTagName("html")[0].setAttribute("lang", lang);

const init = async () => {
  const url = assetsPath + "/docs/json/site-" + lang + ".json";
  const rawSite = await axios.get(url);
  const site = rawSite.data;

  document
    .querySelector('meta[name="apple-mobile-web-app-title"]')
    .setAttribute("content", site.shortTitle);
  document
    .querySelector('meta[name="theme-color"]')
    .setAttribute("content", site.theme.color);
  document
    .querySelector('meta[name="apple-mobile-web-app-status-bar-style"]')
    .setAttribute("content", site.theme.color);
  document
    .querySelector('meta[name="msapplication-navbutton-color"]')
    .setAttribute("content", site.theme.color);

  site.menu.forEach((item) => {
    const content = site.pages[item.name].content;
    const refArr = content.filter((o) => o.id === "ref");
    refArr.forEach((element) => {
      const cont = site.pages[element.refPage].content.find(
        (o) => o.uid === element.refId
      );
      content.push(cont);
    });
  });

  root.render(
    <BrowserRouter>
      <App
        className="App"
        value={{
          lang: lang,
          assetsPath: assetsPath,
          origin: origin,
          dimention: { w: innerWidth, h: innerHeight },
          breakpoint: breakpoint,
          pRatio: devicePixelRatio,
          site: site
        }}
      />
    </BrowserRouter>
  );
};

init();
