import React from "react";
import ReactHtmlParser from "react-html-parser";
import {
  Form,
  Button,
  Col,
  Navbar,
  Nav,
  Container,
  Dropdown,
  Carousel,
  Row,
  Card,
  Image
} from "react-bootstrap";
import { ToastContainer, toast } from "react-toastify";
import { Link } from "react-router-dom";
import "react-toastify/dist/ReactToastify.css";
import axios from "axios";
import { motion } from "framer-motion/dist/framer-motion";
import { capitalizeText } from "./General";

export function Toastify(props) {
  const options = {
    position: "top-center",
    autoClose: 5000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined
  };
  switch (props.type) {
    case "info":
      toast.info(props.text, options);
      break;
    case "success":
      toast.success(props.text, options);
      break;
    case "error":
      toast.error(props.text, options);
      break;
    default:
      break;
  }
}

export function CToastify() {
  return <ToastContainer />;
}

export class CHeader extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      origin: props.value.origin,
      assetsPath: props.value.assetsPath,
      lang: props.value.lang,
      logo: props.value.logo,
      menu: props.value.menu,
      show: false
    };
  }

  handleNavClick(e) {
    if (window.innerWidth <= 992) {
      document.getElementById("basic-navbar-nav-toggle").click();
    }
    window.pageId = e.target.dataset.id;
  }

  render() {
    return (
      <Navbar bg="light" expand="lg" className="pb-4">
        <Container>
          <Navbar.Brand
            as={Link}
            to={"/" + this.state.lang + "/"}
            className="col-3 col-md-2 position-relative"
          >
            {(() => {
              if (this.state.logo.animate.stt) {
                return (
                  <Image
                    style={{ zIndex: "999999" }}
                    className={"col-12 position-absolute"}
                    src={
                      this.state.assetsPath +
                      "/img" +
                      this.state.logo.animate.img
                    }
                    onLoad={(e) => {
                      e.target.classList.add(this.state.logo.animate.class);
                    }}
                    onTransitionEnd={(e) => {
                      e.target.classList.add("fadeOut");
                      e.target.nextElementSibling.classList.add("fadeIn");
                    }}
                  ></Image>
                );
              }
            })()}
            <Image
              className="col-12"
              style={
                this.state.logo.animate.stt ? { opacity: 0 } : { opacity: 1 }
              }
              src={this.state.assetsPath + "/img" + this.state.logo.img}
              alt={this.state.origin.replace("https://", "") + " logo"}
            ></Image>
          </Navbar.Brand>
          <Navbar.Toggle
            aria-controls="basic-navbar-nav"
            id="basic-navbar-nav-toggle"
          />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              {this.state.menu.map((item, index) => (
                <Nav.Link
                  key={index + Math.random()}
                  as={Link}
                  to={"/" + this.state.lang + item.url}
                  className="hvr-sweep-to-right ps-1"
                  onClick={this.handleNavClick}
                  data-id={item.name}
                >
                  {item.txt}
                </Nav.Link>
              ))}
            </Nav>
            <Dropdown>
              <Dropdown.Toggle
                variant="success"
                id="dropdown-basic"
                className="text-capitalize"
              >
                {this.state.lang}
              </Dropdown.Toggle>

              <Dropdown.Menu>
                <Dropdown.Item href="/tr/">Tr</Dropdown.Item>
                <Dropdown.Item href="/en/">En</Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    );
  }
}

export function CPage(props) {
  const state = {
    lang: props.value.lang,
    assetsPath: props.value.assetsPath,
    origin: props.value.origin,
    dimention: props.value.dimention,
    breakpoint: props.value.breakpoint,
    pRatio: props.value.pRatio,
    pages: props.value.pages,
    pageId: props.value.pageId
  };

  document.title = state.pages[state.pageId].metatitle;

  return (
    <div id={state.pageId} data-type="page">
      {state.pages[state.pageId].content.map((item, index) =>
        (() => {
          if (item.id === "banner") {
            return (
              <motion.div
                key={Math.random()}
                className="position-relative mb-5 bg-light"
                initial={{ width: 0 }}
                animate={{ width: "100%" }}
                exit={{ x: window.innerWidth, transition: { duration: 0.1 } }}
                style={{ minHeight: (window.innerWidth * 26) / 100 }}
              >
                <Image
                  className="d-block w-100"
                  src={
                    state.assetsPath +
                    "/img/banner/" +
                    item.img +
                    (state.dimention.w > 768 ? "" : "_md") +
                    ".webp"
                  }
                  alt={item.title}
                  data-animate="animate__zoomIn"
                  onLoad={(e) => {
                    e.target.nextElementSibling.remove();
                  }}
                ></Image>
                <div className="d-flex justify-content-center">
                  <div className="spinner-border" role="status">
                    <span className="visually-hidden">Loading...</span>
                  </div>
                </div>
                <h1 className="bg-white w-auto p-2 position-absolute top-100 start-50 translate-middle">
                  {item.title}
                </h1>
              </motion.div>
            );
          } else if (item.id === "slider") {
            return (
              <motion.div
                initial={{ width: 0 }}
                animate={{ width: "100%" }}
                exit={{ x: window.innerWidth, transition: { duration: 0.1 } }}
              >
                <Carousel
                  id={item.uid}
                  key={Math.random()}
                  className="d-flex"
                  style={{ height: "0" }}
                >
                  {item.text.map((el, idx) => (
                    <Carousel.Item key={idx}>
                      <img
                        className="d-block w-100"
                        src={
                          state.assetsPath +
                          "/img/slides/slide-" +
                          (idx + 1) +
                          state.breakpoint +
                          ".webp"
                        }
                        alt={el}
                        onLoad={(e) => {
                          document
                            .getElementById(item.uid)
                            .classList.add("sliderLaunch");
                        }}
                      />
                      <Carousel.Caption>
                        <h3 className="text-shadow-1">{el}</h3>
                      </Carousel.Caption>
                    </Carousel.Item>
                  ))}
                </Carousel>
              </motion.div>
            );
          } else if (item.id === "card") {
            return (
              <Row
                key={Math.random()}
                className="justify-content-center col-12 col-lg-8 mx-auto my-5"
              >
                {item.title === null ? (
                  ""
                ) : (
                  <h2 className="border-bottom">{item.title}</h2>
                )}
                {item.cards.map((el, idx) => (
                  <Card
                    key={idx + Math.random()}
                    className="m-3 pt-2"
                    style={{ width: "300px" }}
                  >
                    <Card.Img
                      variant="top"
                      src={
                        state.assetsPath +
                        "/img/" +
                        el.img +
                        (state.pRatio > 1 ? "" : "_sm") +
                        ".webp"
                      }
                      alt={el.title === null ? "" : el.title}
                    />
                    <Card.Body>
                      {el.title === null ? (
                        ""
                      ) : (
                        <Card.Title>{el.title}</Card.Title>
                      )}
                      {el.text === null ? (
                        ""
                      ) : (
                        <div>{ReactHtmlParser(el.text)}</div>
                      )}
                      {el.action === null ? (
                        ""
                      ) : (
                        <Button variant="primary">{el.action.text}</Button>
                      )}
                    </Card.Body>
                  </Card>
                ))}
              </Row>
            );
          } else if (item.id === "list-slider") {
            return (
              <Row
                key={Math.random()}
                className="justify-content-center col-12 col-lg-8 mx-auto my-3"
              >
                {item.title === null ? (
                  ""
                ) : (
                  <h2 className="border-bottom">{item.title}</h2>
                )}
                <Carousel variant="dark" className="p-0 my-3">
                  {item.list.map((el, idx) => (
                    <Carousel.Item key={idx + Math.random()}>
                      <div className="d-flex justify-content-around">
                        {el.map((el, idx2) => (
                          <img
                            key={idx2 + Math.random()}
                            style={{ maxWidth: "128px" }}
                            className="img-fluid col-3"
                            src={
                              state.assetsPath +
                              "/img" +
                              el.img +
                              (state.pRatio > 1 ? "" : "_sm") +
                              ".webp"
                            }
                            alt={el.text === null ? "" : el.text}
                          />
                        ))}
                      </div>
                    </Carousel.Item>
                  ))}
                </Carousel>
              </Row>
            );
          } else if (item.id === "p") {
            return (
              <Row
                key={Math.random()}
                className="mx-auto p-3 col-12 col-xxl-7 col-xl-8 col-lg-9 col-md-10 col-sm-12"
              >
                {ReactHtmlParser(item.text)}
              </Row>
            );
          } else if (item.id === "form") {
            return (
              <CForm
                key={Math.random()}
                action={item.action}
                items={item.items}
                lang={state.lang}
                origin={state.origin}
              />
            );
          }
        })()
      )}
    </div>
  );
}

export function CForm(props) {
  //const [validated, setValidated] = useState(false);
  const action = props.action;
  const items = props.items;
  const values = {};
  const lang = props.lang;
  const origin = props.origin;

  const handleSubmit = (event) => {
    const form = event.target.parentElement;

    if (!form.checkValidity()) {
      form.reportValidity();
    } else {
      let resText = "";
      switch (lang) {
        case "en":
          resText = {
            i: "Sending Your Message...",
            s: "Successfully transmitted",
            e: "Something went wrong. Please try again."
          };
          break;
        case "tr":
        default:
          resText = {
            i: "Mesajınız Gönderiliyor...",
            s: "Başarıyla iletildi.",
            e: "Bir hata oluştu. Lütfen tekrar deneyin."
          };
          break;
      }

      Toastify({ type: "info", text: resText.i });

      const elements = form.elements;
      for (let i = 0, len = elements.length; i < len; i++) {
        values[elements[i]["name"]] = elements[i]["value"];
      }

      const subject =
        capitalizeText(window.pageId) + " - Form (" + origin + ")";

      let messageHtml = "<h1>" + subject + "</h1><br>";
      for (const prop in values) {
        if (prop !== "from" && prop !== "button") {
          messageHtml +=
            "<div><b>" + prop + ": </b>" + values[prop] + "</div><hr></br>";
        }
      }

      const data = {
        entry: {
          subject: subject,
          from: values["from"],
          to: values["from"],
          messageHtml: messageHtml
        }
      };
      const options = {
        method: "POST",
        data: JSON.stringify(data),
        url: props.origin + "/sendmail/"
      };

      axios(options)
        .then(function (response) {
          Toastify({ type: "success", text: resText.s });
          form.remove();
        })
        .catch(function (error) {
          Toastify({ type: "success", text: resText.e });
        });
    }
    //setValidated(true);
  };

  return (
    <Form
      //noValidate
      //validated={validated}
      className="p-3 col-12 col-md-6 mx-auto border"
    >
      {items.map((el, idx) => (
        <Form.Group
          key={Math.random()}
          as={Col}
          controlId={Math.random()}
          className="mt-4"
        >
          <Form.Label>{el.text}</Form.Label>
          {el.type === "textarea" ? (
            <Form.Control required as="textarea" name={el.id} rows={4} />
          ) : (
            <Form.Control
              required
              type={el.type}
              name={el.id}
              placeholder={el.text}
              defaultValue={el.value || ""}
            />
          )}
        </Form.Group>
      ))}
      <Button
        variant="primary"
        type="button"
        name="button"
        className="mt-0"
        onClick={handleSubmit}
      >
        {action.text}
      </Button>
    </Form>
  );
}
